@import url(//fonts.googleapis.com/css?family=Raleway|Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

body,
label,
input,
button,
select {
  font-family: Roboto; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none; }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.form-top-area,
.form-body-area {
  border-radius: 4px;
  border: 1px solid #e5e9ec;
  background-color: #ffffff;
  margin-bottom: 30px; }

.form-body-area .col-md-9 {
  border-right: 1px solid #e5e9ec; }

.App h1 {
  /* Style for "Subscripti" */
  color: #003345;
  font-family: Raleway;
  font-size: 26px;
  font-weight: 700;
  letter-spacing: 0.6px;
  line-height: 30px; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.App .sidebar {
  /* Style for "Rectangle" */
  width: 300px;
  margin-right: 50px;
  height: 2000px;
  background-color: #003345; }

.App .page-content {
  max-width: 1060px; }

.App legend {
  /* Style for "Widget Pre" */
  color: #0f7292;
  font-family: Raleway;
  font-size: 18px;
  font-weight: 700;
  letter-spacing: 0.4px;
  line-height: 28px;
  border: none;
  padding-top: 20px; }

.btn {
  border-radius: 100px; }

.btn-group .btn {
  border-radius: 4px; }

.form-actions {
  padding: 25px 0;
  text-align: center; }

.form-actions .btn {
  width: 100%; }

.widgetPreview pre {
  text-overflow: auto;
  height: 200px;
  font-size: 9px; }

.form-control {
  /* Style for "Rectangle" */
  width: 120px;
  height: 42px;
  border-radius: 4px;
  border: 1px solid #d0d4d7;
  background-color: #ffffff; }

.rjsf .form-inline legend,
.rjsf .field-array legend,
.rjsf label {
  /* Style for "Frequency" */
  padding: 0;
  font-weight: normal;
  height: 40px;
  color: #333333;
  font-family: Roboto;
  font-size: 14px;
  letter-spacing: 0.2px;
  line-height: 20px;
  padding: 20px 0 0; }

.form-group .control-label {
  float: left;
  width: 20%; }

.form-group .control-label + * {
  width: 80%;
  margin-right: 5px; }

.form-inline fieldset > .form-group {
  display: inline-block;
  vertical-align: middle; }

.form-group.form-inline {
  /* margin-bottom: 0; */ }

.form-inline .control-label {
  display: none; }

/* .form-group.form-inline .form-group .control-label + * { */
/* width: inherit; */
/* } */
.field-number .form-control {
  width: 80px; }

.form-group .field-array .array-item-list {
  float: left;
  width: 60%; }

.array-item .array-item-toolbox {
  visibility: hidden;
  left: -75px;
  top: 5px; }

.field-array .array-item-add {
  margin-left: 19.4%; }

.array-item:hover .array-item-toolbox {
  visibility: visible; }

.form-group .field-array legend,
.form-inline fieldset > legend {
  float: left;
  width: 20%;
  font-size: inherit;
  border: none;
  margin: 0; }

.form-inline .form-control {
  width: auto; }

.form-group .control-label + .rdw-editor-wrapper {
  width: 100%; }

.rdw-editor-main {
  border: 1px solid #ccc;
  margin-left: 20%;
  border-radius: 4px;
  border: 1px solid #d0d4d7;
  background-color: #ffffff;
  padding: 6px 12px; }

.App .nav-pills {
  margin: 0 -15px; }

.App .nav-pills li {
  margin: 15px 0; }

.App .nav-pills li.active {
  background-color: #003345; }

.App .nav-pills a {
  color: #979ea6;
  font-family: Raleway;
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 1px;
  line-height: 20px;
  text-transform: uppercase;
  border-radius: 0; }

.App .nav-pills li a:hover {
  background-color: #144455; }

.App .nav-pills li.active a {
  background-color: #144455;
  color: #00a7e1; }

.field-boolean input {
  position: absolute;
  left: 23%; }

#contextHelp {
  position: absolute;
  -webkit-transition: all 0.4s ease 0.2s;
  transition: all 0.4s ease 0.2s;
  top: 0;
  /* start out at position 0 */ }

#contextHelp .badge-info {
  background: #09f;
  position: absolute;
  top: 10px;
  left: -25px; }

#contextHelp h3 {
  margin-top: 8px; }

